import React from 'react';
import Card from './Card';


const CardList = ({ robots }) => {
	const onhandleClick = (event) => {
		const infoPage=window.open("", "infoPage", "top=180, left=430, width=420, height=520");
		infoPage.document.write(
	  			"<img alt='Robot' src='https://robohash.org/" + robots[event.currentTarget.id].id + "?250x250' />" + "<br> <br>" + 
	  			"<strong>Name: </strong>" + robots[event.currentTarget.id].name + "<br>" +
	  			"<strong>Username: </strong>" + robots[event.currentTarget.id].username + "<br>" +
	  			"<strong>Email: </strong>" + robots[event.currentTarget.id].email + "<br>" +
	  			"<strong>Website: </strong>" + robots[event.currentTarget.id].website + "<br>" +
	  			"<strong>Phone number: </strong>" + robots[event.currentTarget.id].phone + "<br>" +
	  			"<strong>Company Name: </strong>" + robots[event.currentTarget.id].company.name + "<br> <br>" +
	  			"<button type='button' onClick='window.close()'> Close </button>"
  		);
  		infoPage.document.body.style.background = "linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%)";
  		infoPage.document.body.style.textAlign = "center";
  		infoPage.document.body.style.lineHeight = "1.3";
  		infoPage.document.body.style.color = '#9eebcf';
  		infoPage.focus();
	}
	return (
		<div>
			{
				robots.map((user, i) => {
					return (
						<Card 
							key={i}
							num={i}
							id={robots[i].id}
							name={robots[i].name}
							email={robots[i].email}
							handleClick={onhandleClick}
						/>						
					);
				})
			}
		</div>
	);
}

export default CardList;